@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Ubuntu;
  scroll-behavior: smooth;
}
*::-webkit-scrollbar {
  display: none;
}
body {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: #202020;
  overflow-y: scroll;
}
html {
  font-size: 62.5%;
}
#cont {
  position: relative;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: grid;
  grid-template-columns: 7rem auto;
  overflow-y: scroll;
}
@media screen and (max-width: 768px) {
  html {
    font-size: 55%;
  }
  #cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 1rem 2rem;
  }
}
@media screen and (max-width: 400) {
  html {
    font-size: 20%;
  }
}
@media screen and (max-width: 300) {
  html {
    font-size: 10%;
  }
}

header {
  width: 100%;
  height: 6rem !important;
  display: flex;
  align-items: center;
  background-color: #222222f8;
  padding: 1.1rem 2.5rem;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 100;
}
header .bars {
  font-size: 2rem;
}
header > #search-box {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  min-width: 20rem;
  height: 3.3rem;
  margin-left: 1.5rem;
  background-color: #0e0e0e;
}
header .logo {
  display: flex;
  align-items: center;
}
header .logo img {
  width: 3rem;
  height: 3rem;
  object-fit: cover;
}
header .logo > h2 {
  font-size: 1.6rem;
}
header .logo > h2 span {
  font-size: 2.5rem;
  color: greenyellow;
}
header .logo .bars {
  margin-right: 2rem;
}
#search-box > .mainbox {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}
#search-box > .mainbox input {
  font-family: Roboto !important;
  width: 90%;
  height: 100%;
  border: none;
  padding-left: 1rem;
  background-color: transparent;
  z-index: 2;
  color: white;
  border: 1px solid #383838;
}
#search-box > .mainbox > input::-webkit-input-placeholder {
  font-weight: 600;
  font-size: 1.5rem;
}
#search-box > .mainbox > input:-ms-input-placeholder {
  font-weight: 600;
  font-size: 1.5rem;
}
#search-box > .mainbox > input::placeholder {
  font-weight: 600;
  font-size: 1.5rem;
}
#search-box > .mainbox > input:focus {
  outline: 1px solid #1884ff;
}
#search-box > .mainbox .linkelem {
  position: relative;
  width: 10%;
  min-width: 3rem;
  flex-grow: 1;
  height: 100%;
  display: grid;
  place-items: center;
  color: white;
  font-size: 1.5rem;
  background-color: #383838;
}

#tool {
  display: flex;
  align-items: center;
  color: white;
  font-size: 1.8rem;
  margin-left: 1rem;
}
#tool .t {
  margin: 0px 1.5rem;
}
#tool .t:hover {
  color: greenyellow;
}
#profile {
  min-width: 3rem;
  min-height: 3rem;
  width: 6rem;
  height: 4rem;
  margin-left: 1rem;
}
#profile img {
  width: 100%;
  border-radius: 50%;
  height: 100%;
  object-fit: cover;
}
@media screen and (max-width: 768px) {
  #profile {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  #tool {
    display: none;
  }
}

#nav {
  width: 7rem;
  height: 100%;
  background-color: #222222f8;
  position: fixed;
}
#nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#nav ul li {
  list-style: none;
  margin-top: 1rem;
  width: 100%;
  height: 7rem;
  display: grid;
  place-items: center;
  text-align: center;
  cursor: pointer;
  color: white;
}
#nav ul li a {
  text-decoration: none;
  color: white;
  font-size: 1.8rem;
}
#nav ul li:hover a {
  color: greenyellow;
}
#nav ul li:hover {
  background-color: #3b3b3b;
}
#nav ul li a > span {
  display: block;
  font-size: 1rem;
  color: white;
}
@media screen and (max-width: 800px) {
  #nav {
    display: none;
  }
}

#box {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 22rem;
  opacity: 0;
  bottom: 0px;
  z-index: 9999;
  background-color: #222222;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  color: white;
  padding-top: 1.5rem;
  overflow: hidden;
  transform: translateX(-22rem);
  transition-timing-function: ease-out;
  transition: 0.2s;
}
#box.transition {
  transform: translateX(0px);
  opacity: 1;
}
#box-wrap {
  overflow-y: scroll;
  margin-top: 2rem;
}
#box-wrap::-webkit-scrollbar {
  display: none;
}

#box-logo {
  display: flex;
  align-items: center;
  margin-left: 2.5rem;
}
#box-logo > img {
  width: 3rem;
  height: 3rem;
  object-fit: cover;
}
#box-logo > h2 span {
  font-size: 2.5rem;
  color: greenyellow;
  margin-left: 0.2rem;
}
#box-logo > h2 {
  font-size: 1.6rem;
}
hr {
  border: none;
  border-top: 0.5px solid #4e4e4eb6;
}
#box .features > li,
#box .subscription > li,
#box .more > li {
  list-style: none;
  margin: 1rem 0px;
  display: flex;
  padding: 1rem 0px;
  align-items: center;
  width: 100%;
  color: #dadada;
  font-size: 1.6rem;
  transition: 0.2s;
  padding-left: 2.5rem;
  cursor: pointer;
}

#box .icon {
  font-size: 2rem;
  margin-right: 2rem;
}
#box .bar {
  color: greenyellow;
}
#box li:hover {
  transform: scale(1.05);
  background-color: #8b8b8b81;
  color: greenyellow !important;
}

main {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 1.5rem;
  grid-row-gap: 3rem;
  grid-column-start: 2;
  width: 100%;
  height: 100%;
  background-color: #1b1b1b;
  padding: 2rem 2rem;
  place-items: center;
}
@media screen and (max-width: 950px) {
  main {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 850px) {
  main {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 515px) {
  main {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 1rem 2rem;
  }
}

#vcont {
  position: relative;
  display: flex;
  flex-direction: column;
  color: white;
  -width: 30rem;
}
@media screen and (max-width: 768px) {
  #vcont {
    max-width: 35rem;
  }
}
#vcont:hover {
  cursor: pointer;
}
#vcont a {
  text-decoration: none;
  border: none;
}
#vcont a:focus {
  outline: none;
}
#imageBox {
  position: relative;
  width: 100%;
  height: 15rem;
}
#imageBox img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
#imageBox .duration {
  position: absolute;
  display: inline-block;
  right: 0.5rem;
  bottom: 0.5rem;
  width: 4rem;
  height: 2rem;
  background-color: #000000cb;
  text-align: center;
  display: grid;
  place-items: center;
  border-radius: 0.2rem;
}
#vcont > img::before {
  content: "";
  position: absolute;
  right: 0px;
  bottom: 0px;
  width: 3rem;
  height: 2rem;
  background-color: black;
  color: white;
  border: 1px solid red;
}
#ch-cont {
  margin-top: 1.5rem;
  display: flex;
}
#ch-cont img {
  display: inline-block;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
#ch-cont .detail {
  margin-left: 1.5rem;
}
#ch-cont .detail h3 > a {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  width: 17rem;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  font-family: Roboto;
  color: white;
  font-size: 1.6rem;
  font-weight: 500;
}
#ch-cont .detail {
  color: #b1b1b1;
  font-size: 1.3rem;
}
#ch-box {
  margin-top: 1rem;
  font-size: 1.3rem;
}
#ch-box .ch-name {
  color: #b1b1b1;
  font-size: 1.6rem;
  display: flex;
  align-items: center;
}
#ch-box .ch-name .v {
  font-size: 1.2rem;
}
#ch-box .ch-name a {
  color: inherit;
}
#ch-box .ch-de {
  display: flex;
  align-items: center;
}
#ch-box .ch-de .dot {
  font-size: 0.5rem;
}

#main_cont {
  position: relative;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-column-start: 2;
}
#toggle-filter {
  position: relative;
  display: flex;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: 100%;
  flex-direction: column;
  color: white;
  padding: 2rem;
  font-family: Roboto;
  background-color: #131313;
}
@media screen and (max-width: 768px) {
  #toggle-filter {
    display: none;
  }
}
#flt-icon {
  color: #cccccc;
  display: flex;
  align-items: center;
}
#flt-icon .filterbtn {
  font-size: 2rem;
  margin-right: 1rem;
}
#flt-icon span {
  font-size: 1.7rem;
}
#filter-items {
  display: flex;
  margin-top: 3rem;
}
#filter-items ul {
  width: 18rem;
  margin-right: 2.5rem;
}
#filter-items ul h2 {
  color: white;
  font-size: 1.4rem !important;
}
#filter-items ul li {
  margin: 1.5rem 0px;
  font-size: 1.4rem;
  color: #9b9b9b;
  list-style: none;
  cursor: pointer;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
#toggle-filter hr {
  margin-top: 1.4rem;
}
#srch-res {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #131313;
  overflow-y: scroll;
  margin-top: -44rem;
  transition: 0.3s;
  padding: 0 3rem;
}
#srch-res.toggle {
  margin-top: 0px;
}
#srch-res hr {
  margin: 1.5rem 0px;
}

.videoFrame {
  display: flex;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin-bottom: 2rem;
  align-items: center;
}
#img_box {
  position: relative;
  width: 35rem;
  height: 20rem;
}
@media screen and (max-width: 768px) {
  #img_box {
    height: 10rem;
    min-width: 13rem;
  }
}
#img_box > #durationBox {
  position: absolute;
  display: inline-block;
  right: 0.5rem;
  bottom: 0.5rem;
  width: 4rem;
  height: 2rem;
  background-color: #000000b2;
  text-align: center;
  display: grid;
  place-items: center;
  border-radius: 0.2rem;
}
#durationBox > span {
  color: white;
  font-weight: 600;
}
#img_box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.frame_de {
  margin-left: 1rem;
  width: 65%;
  height: 100%;
}
#frame_title {
  color: white;
  font-size: 1.7rem;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
@media screen and (max-width: 768px) {
  #frame_title {
    font-size: 1rem;
  }
}
#views_count {
  color: #8f8f8f;
  font-size: 1.4rem;
  margin-top: 1rem;
  display: flex;
  align-items: center;
}
@media screen and (max-width: 768px) {
  #views_count {
    font-size: 1rem;
  }
}
#views_count .dot {
  font-size: 0.5rem;
}
#ch_de {
  display: flex;
  align-items: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-top: 1rem;
}
#ch_de img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
@media screen and (max-width: 768px) {
  #ch_de img {
    width: 2rem;
    height: 2rem;
  }
}
#ch_de span {
  margin-left: 1.5rem;
  color: #8f8f8f;
  font-size: 1.4rem;
}
@media screen and (max-width: 768px) {
  #ch_de span {
    font-size: 1rem;
  }
}
#dis {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  color: #8f8f8f;
  font-size: 1.4rem;
  margin-top: 1.5rem;
}
@media screen and (max-width: 768px) {
  #dis {
    display: none;
  }
}

#watch_box {
  position: relative;
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  overflow: hidden;
  padding: 1rem 2rem;
}
#video_box {
  position: relative;
  width: 65%;
  height: 100vh;
  overflow-y: scroll;
  margin-top: 2rem;
  margin-right: 2rem;
}
#video_box > #video_panel {
  position: relative;
  width: 100%;
  height: 38rem;
}
@media screen and (max-width: 768px) {
  #video_box > #video_panel {
    height: 25rem;
  }
}
#video_panel > iframe {
  width: 100%;
  height: 100%;
}
#video_box .video_title {
  color: white;
  font-size: 1.7rem;
  margin-top: 1.5rem;
}
#video_box .video_details {
  display: flex;
  align-items: center;
  color: white;
  justify-content: space-between;
  margin-top: 1.5rem;
}
#views {
  color: #9c9c9c;
  font-size: 1.5rem;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  align-items: center;
}
#views .dot {
  font-size: 0.5rem;
}
.likeDislikeBox {
  display: flex;
  align-items: center;
  color: #9c9c9c;
  font-size: 2.3rem;
}
.likeDislikeBox:hover {
  cursor: pointer;
}
.likeDislikeBox span {
  margin: 0px 0.7rem;
  font-size: 1.5rem;
}
#video_box .ch_meta_details {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: -webkit-max-content 1fr -webkit-max-content;
  grid-template-columns: max-content 1fr max-content;
  align-items: center;
}
#video_box .ch_meta_details h3 {
  font-size: 1.3rem;
  color: white;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  align-items: center;
}
#video_box .ch_meta_details h5 {
  margin-top: 0.3rem;
  font-size: 1.2rem;
  color: #9c9c9c;
}
#video_box .ch_meta_details button {
  border: none;
  color: white;
  background-color: #db0049;
  border-radius: 0.2rem;
  padding: 1rem 3rem;
}
#video_box .ch_meta_details button:hover {
  cursor: pointer;
}
#video_box .ch_meta_details img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  margin-right: 1.5rem;
}
#video_box .ch_discription {
  margin-top: 2rem;
  color: #bdbdbd;
  font-size: 1.5rem;
  padding-left: 5rem;
  overflow: hidden;
  transition: 0.5s;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#show_more {
  color: #0018f5;
  font-size: 1.5rem;
  margin-top: 1rem;
  margin-left: 5rem;
  font-weight: bolder;
}
#show_more::selection {
  background-color: transparent;
}
#show_more:hover {
  cursor: pointer;
}
#video_box .ch_discription.active {
  white-space: normal;
}
#cmntCount {
  margin-top: 2rem;
  color: #e7e7e7;
  font-size: 1.6rem;
}
#input_box {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
}
#input_box input {
  color: white;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid gray;
  flex: 1 1;
}
#input_box input:focus {
  outline: none;
  border-bottom: 1px solid royalblue;
}

#input_box button {
  border: none;
  padding: 1rem 3rem;
  background-color: #818181;
  color: #ffffff;
  border-radius: 0.2rem;
}
#input_box button:hover {
  cursor: pointer;
}
#side_playlist {
  position: relative;
  width: 35%;
  height: 100vh;
  padding: 1rem 0px;
  padding-right: 1rem;
  overflow-y: scroll;
}

#cmntCont {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin-top: 2rem;
}
#side_playlist_responsive {
  display: none;
}

@media screen and (max-width: 950px) {
  #side_playlist_responsive {
    display: flex;
    flex-direction: column;
    margin: 2rem 0px;
  }
  #side_playlist {
    display: none;
  }
  #video_box {
    width: 100%;
    margin-right: 0;
  }
}

#cmntbox {
  width: 100%;
  display: flex;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  margin: 2rem 0px;
}
#cmntbox img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
#cmnt_details {
  color: white;
  margin-left: 1rem;
}
#cmnt_details h3 {
  margin-top: 1rem;
  font-size: 1.4rem;
  color: white;
  font-weight: 500;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
#cmnter {
  font-size: 1.3rem;
  color: #bebebe;
}
#cmnter span {
  margin-left: 1rem;
}

#vidbox {
  position: relative;
  width: 100%;
  height: 11rem;
  display: flex;
  margin: 1rem 0px;
}
#vidbox:hover {
  cursor: pointer;
}
.imageframe {
  position: relative;
  flex-shrink: 0;
  width: 18rem;
  height: 100%;
}
#duration_box {
  position: absolute;
  width: 3.2rem;
  height: 1.8rem;
  background-color: #000000cb;
  color: white;
  bottom: 0.5rem;
  right: 0.5rem;
  display: grid;
  place-items: center;
  border-radius: 0.2rem;
}
.imageframe img {
  width: 100%;
  height: 100%;
}
.frametitle {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-left: 0.5rem;
  font-size: 1.2rem;
  font-weight: 500;
  color: white;
}
.frame_channel_title {
  color: #b4b4b4;
  font-size: 1.2rem;
  margin-left: 0.5rem;
  margin-top: 1rem;
}
.frame_views_detail {
  display: flex;
  align-items: center;
  color: #b4b4b4;
  font-size: 1.2rem;
  margin-left: 0.5rem;
  margin-top: 1rem;
}
.dot {
  font-size: 0.4rem;
}

#full_size_cont {
  width: 100%;
  height: 100vh;
  border-color: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
}
#box_cont {
  width: 25rem;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  border: 0.1rem dashed #00f5d4;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 3.5rem;
}
#box_cont img {
  width: 10rem;
  height: 10rem;
}
#box_cont h3 {
  margin-top: 0.5rem;
  font-size: 2rem;
  color: white;
}
#box_cont h3 > span {
  font-size: 2.2rem;
  color: #20ffe1;
}
#box_cont button {
  padding: 1.5rem 2.3rem;
  border: none;
  background-color: #c5005c;
  border-radius: 0.2rem;
  font-size: 1.4rem;
